import {
    beforeEachProviders,
    describe,
    expect,
    it,
    inject
} from '@angular/core/testing';
import {MediaPlannerAppComponent} from '../app/media-planner.component';

beforeEachProviders(() => [MediaPlannerAppComponent]);

describe('App: MediaPlanner', () => {
    it('should create the app',
        inject([MediaPlannerAppComponent], (app:MediaPlannerAppComponent) => {
            expect(app).toBeTruthy();
        }));

    it('should have as title \'media-planner works!\'',
        inject([MediaPlannerAppComponent], (app:MediaPlannerAppComponent) => {
            expect(app.title).toEqual('media-planner works!');
        }));
});
